import { createRouter, createWebHashHistory } from "vue-router";
import Index from "../views/index";

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index
  },
  {
    path: "/user",
    name: "User",
    component: () => import(/* webpackChunkName: "Block" */ "../views/user")
  },
  {
    path: "/user/detail/:address",
    name: "UserDetail",
    component: () => import(/* webpackChunkName: "Block" */ "../views/user/detail")
  },
  {
    path: "/invest",
    name: "Invest",
    component: () => import(/* webpackChunkName: "Block" */ "../views/invest")
  },
  {
    path: "/withdraw",
    name: "Withdraw",
    component: () => import(/* webpackChunkName: "Block" */ "../views/withdraw")
  }
  
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
